import React from 'react'
import Tab from './Tab'
import { Link } from 'react-router-dom';
const Detail = (props) => {

    return (
        <div>
            <img src={`${props.location.src}`} style={{ width: '100%', position: 'absolute' }} />
            <Link to='/home'>
                <button
                    style={{
                        width: '30px',
                        height: '30px',
                        background: 'rgba(0, 0, 0, 0.4)',
                        color: '#fff',
                        fontSize: '20px',
                        borderRadius: '100%',
                        position: 'relative',
                        margin: '15px'
                    }}
                ><span>&lt;</span></button>
            </Link>
            <Tab src={`${props.location.src}`} />
        </div>
    )
}
export default Detail;