let datalist = [], index = [], userinfo = '';

const datalistreducer = (state = datalist, action) => {
    // console.log(action.list);
    switch (action.type) {
        case 'GETLIST':
            return action.list;//返回商品列表
        default:
            return state;

    }
}

const indexreducer = (state = index, action) => {
    console.log(action.index);
    switch (action.type) {
        case 'ADD_LIST':
            if (state.indexOf(action.index) < 0) {
                return [...state, action.index];
            }
            else {
                return state;
            }
        case 'REMOVE_LIST':
            let newData = JSON.parse(JSON.stringify(state));//删除数据
            newData.splice(action.index, 1)
            return newData;
        default:
            return state;

    }
}
const userinforeducer = (state = userinfo, action) => {
    console.log(action.username);
    switch (action.type) {
        case 'LOGIN':

            return { userinfo: action.username };
        default:
            return state;
    }
}

export { datalistreducer, indexreducer, userinforeducer }