import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '/home',
        };
    }
    render() {
        const { pathname } = this.props.location;
        console.log(this.props.location);
        return (
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0,
            }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye1'></i>
                        }
                        selected={pathname === '/home'}
                        onPress={() => {
                            this.props.history.push('/home');
                            // {this.props.userinfo?}
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuche'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwuche1'></i>
                        }
                        title="收藏"
                        key="cart"
                        selected={pathname === '/buy'}
                        onPress={() => {
                            this.props.username ? this.props.history.push('/buy') : this.props.history.push('/login')

                        }}
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        selectedIcon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(https://zos.alipayobjects.com/rmsportal/IIRLrXXrFAhXVdhMWgUI.svg) center center /  21px 21px no-repeat'
                            }}
                            />
                        }
                        title="订单列表"
                        key="Friend"
                        selected={pathname === '/list'}
                        onPress={() => {
                            this.props.history.push('/list')
                        }}
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode'></i>}
                        selectedIcon={{ uri: 'https://zos.alipayobjects.com/rmsportal/gjpzzcrPMkhfEqgbYvmN.svg' }}
                        title="我的淘宝"
                        key="my"
                        selected={this.state.selectedTab === 'yellowTab'}
                        selected={pathname === '/my'}
                        onPress={() => {
                            this.props.history.push('/my')
                        }}
                    >

                    </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return {
        username: state.userinfo
    }
};

export default connect(mapStateToProps)(withRouter(Mytab));

