import React from 'react'
import { connect } from 'react-redux';
import { login } from './get-login';

const Login = (props) => {
    console.log(props.username.userinfo)
    return (
        <div>
            {props.username ?
                <div>

                    {props.username.userinfo}
                    <button onClick={() => {
                        props.dispatch({
                            type: 'LOGIN',
                            username: ''
                        })
                    }}>退出</button>
                    <button onClick={() => {
                        props.history.push('/home');
                    }}>返回</button>
                </div>
                : <button onClick={() => {
                    props.dispatch(login())
                }}
                >登录</button>

            }

        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        username: state.userinfo
    }
};

export default connect(mapStateToProps)(Login)
