import React from 'react'
import { connect } from 'react-redux';
import Mytab from './Mytab'

const Buy = (props) => {
    console.log(props)
    return (
        <div>
            {
                props.index.map((item, index) => {
                    return <div
                        style={{
                            width: '40%',
                            marginLeft: '25px',
                            float: 'left',
                            marginTop: '12px',
                            backgroundColor: 'white',
                            marginBottom: '30px'
                        }}

                    >
                        <img style={{ width: '100%' }} src={`${item}`} />
                        <button onClick={() => {
                            props.dispatch({
                                type: 'REMOVE_LIST',
                                index: props.index.indexOf(item)

                            })
                            console.log(props.index.indexOf(item))
                        }}>删除</button>
                    </div>
                })
            }

            <Mytab />
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        index: state.index
    }
};

export default connect(mapStateToProps)(Buy);
