
import React, { useEffect } from 'react'
import { SearchBar } from 'antd-mobile';
import CarouselLower from './CarouselLower'
import Mytab from './Mytab'
import { connect } from 'react-redux';
import { getlist } from './get-login';


const Home = (props) => {
    useEffect(() => {
        props.dispatch(getlist())
    }, [])
    return (
        <div>
            <SearchBar
                placeholder="请输入宝贝店铺"
                onFocus={() => props.history.push('/home/search')}
            />
            <CarouselLower />
            {
                props.arry.map((item, idx) => {
                    return <div
                        style={{
                            width: '40%',
                            marginLeft: '25px',
                            float: 'left',
                            marginTop: '12px',
                            backgroundColor: 'white',
                            marginBottom: '30px'
                        }}

                        onClick={() => {
                            props.history.push({ pathname: '/home/detail', src: `${item.img}` })
                        }}


                    // to='/home/detail'
                    >
                        <img style={{ width: '100%' }} src={`${item.img}`} />

                        <div style={{ marginBottom: '5px', marginLeft: '3px', height: '70px', color: '#333', fontSize: '0.9rem' }}>
                            {`${item.title}`}<br />
                      ￥ {`${item.price}`}<br />
                      99999+人已购买
                  </div>
                    </div>
                })
            }
            <Mytab />
        </div>
    )
}


const mapStateToProps = (state) => {
    return {
        arry: state.datalist
    }
};

export default connect(mapStateToProps)(Home)